.dsp {
  .dataTables_scroll {
    margin-top: 20px;
  }
  
  .dataTables_info {
    padding-left: 90px;
  }

  th {
    &:nth-child(1), 
    &:nth-child(2), 
    &:nth-child(3) {
      visibility: hidden;
    }
    font-weight: bold;
    font-size: 16px;
    color: #164B89;
  }

  tr.group {
    font-size: 36px;
    font-weight: bold;
  }

  .table>thead>tr>th.sorting {
    border-bottom: none;
  }

  tr:not(.group) td:nth-child(1) {
    width: 1%;
    text-align: center;
  }

  tr:not(.group) td:nth-child(2) {
    //padding-left: 30px !important;
    width: 40%;
    a {
      color: #164B89;
      font-weight: bold;
    }
  }

  td:nth-child(3), th:nth-child(3) {
    display: none;
  }

  table.dataTable tr.group td {
    background-color: transparent !important;
    border-bottom: 3px solid #164B89;
    padding-left: 90px;
    color: #164B89;
  }

  .table-wrapper {
    background: url(../images/bg-w.png) repeat-y left top;
    background-size: 600px;
  }
  
  td {
    img {
      transition: width 0.1s;
      &:hover {
        width: 135px;
      }
    }
  }
  
  tr.marked {
    background-color: lighten(#14ae56, 53%) !important;
  }
}