.dsp-detail {
  .jumbotron h1 {
    font-weight: bold;
  }
  
  table {
    tr {
      td {
        font-size: 16px;
        padding-top: 18px !important;
        padding-bottom: 18px !important;
      }
    }
    tr td:nth-child(1) {
      text-align: right;
      font-weight: bold;
      color: #14AE56;
      width: 35%;
    }
    tr.sub {
      background-color: #fcfcfc;
      td:nth-child(1) {
        font-weight: normal;
      }
    }
    &.single {
      tr td:nth-child(1) {
        padding-right: 30px;
        width: 40%;
      }
      tr td:nth-child(2) {
        padding-left: 30px;
      }
    }
  }
  
  a {
    color: #14AE56;
  }
  
  tr {
    &.header {
      td {
        border: none;
        background: #f6f6f6;
        border-right: 4px solid #ffffff;
        h2 {
          font-weight: bold;
          font-size: 2em;
          margin: 0;
        }
        vertical-align: middle;
      }
    }
    &.subheader {
      td {
        padding-bottom: 0 !important;
        text-align: left !important;
        border-bottom: 3px solid #14ae56;
      }
    }
    &.subheader-first {
      td {
        border-top: none;
      }
    }
  }
  
}