/**
 * Styles for vertically centered blocks.
 */
.block-fill-height {
  display: table;
  width: 100%;
}
.block-xs-top,
.block-xs-bottom,
.block-xs-middle {
  display: table-cell;
  vertical-align: middle;
}
.block-xs-bottom {
  vertical-align: bottom;
}
.block-xs-top {
  vertical-align: top;
}
@media (min-width: 768px) {
  .block-sm-top,
  .block-sm-bottom,
  .block-sm-middle {
    display: table-cell;
    vertical-align: middle;
  }
  .block-sm-bottom {
    vertical-align: bottom;
  }
  .block-sm-top {
    vertical-align: top;
  }
}
@media (min-width: 992px) {
  .block-md-top,
  .block-md-bottom,
  .block-md-middle {
    display: table-cell;
    vertical-align: middle;
  }
  .block-md-bottom {
    vertical-align: bottom;
  }
  .block-md-top {
    vertical-align: top;
  }
}
@media (min-width: 1200px) {
  .block-lg-top,
  .block-lg-bottom,
  .block-lg-middle {
    display: table-cell;
    vertical-align: middle;
  }
  .block-lg-bottom {
    vertical-align: bottom;
  }
  .block-lg-top {
    vertical-align: top;
  }
}

/**
 * Styles for cover images.
 */
.cover {
  background-size: cover;
  background-position: center top;
  height: 100vh;
}