* {
  font-family: 'Avenir Next W01';
  font-weight: 400;
}

p, .lead {
  font-weight: 400 !important;
}

h1 {
  font-weight: bold;
}

#header {
  border-top: 20px solid black;
  background: linear-gradient(#5D88BA, #164B89);
  padding: 15px;
  padding-top: 5px;
  margin-bottom: 20px;
  h5 {
    color: #ffffff;
  }
  h1 {
    font-weight: bold;
    color: #ffffff;
  }
  &.light {
    background: linear-gradient(#ffffff, #ededed);
    h5 {
      color: #14AE56 !important;
      font-size: 18px;
      font-weight: bold;
    }
    border-bottom: 1px solid gray;
  }
  //position: fixed;
}

#example_wrapper .row:first-child {
  margin-top: -60px;
  input {
    border-radius: 1000px;
  }
}

.logos {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  div {
    text-align: center;
  }
  img {
    display: inline-block;
    max-height: 70px;
    margin: 0 20px 20px;
  }
}

.img-responsive {
  display: inline-block !important;
}

.bg-black {
  background: #000000;
  color: #ffffff;
}

.bg-light-gray {
  background: #f2f2f2;
}

.bg-dark-gray {
  background: #666666;
  color: #ffffff;
}

.bg-dark-gray-gradient {
  background: linear-gradient(#456151, #444444);
  color: #ffffff;
}

.bg-light-gray-gradient {
  background: linear-gradient(#f2f2f2, lighten(#14ae56, 53%));
}

.bg-light-green-gradient {
  background: linear-gradient(lighten(#14ae56, 53%), lighten(#14ae56, 49%));
}

.jumbotron {
  hr {
    border-bottom: 1px solid #ccc;
    margin-left: 45%;
    margin-right: 45%;
  }
  .tagline {
    color: #14AE56;
  }
  &.header {
    background: linear-gradient(#f6f6f6, #ADDBC1);
  }
  .logo {
    margin-bottom: 30px;
  }
}

#example_filter {
  input {
    width: 200px;
  }
}

#header {
  position: relative;
  .comparison-button {
    background: url(../images/arrow.png) no-repeat left 10px;
    background-size: 50px;
    position: absolute;
    top: 130px;
    left: 57px;
    padding-left: 40px;
    z-index: 1000;
    &.active {
      a {
        background-color: #14AE56;
      }
    }
    a {
      border-radius: 1000px;
      background: #164B89;
      color: #ffffff;
      font-weight: bold;
      border: none;
      transition: background 0.5s;
      &:hover {
        background: #14ae56;
      };
    }
  }
}

.t-green {
  .jumbotron {
    padding: 20px;
    border-radius: 0 !important;
  }
  h1, h2, h3, h4, h5, h6 {
    color: #14ae56 !important;
  }
//  .group-header {
//    border-bottom: 3px solid #14ae56;
//  }
}

ul {
  li {
    padding-bottom: 20px;
  }
}

.data-label {
  ul {
    list-style-type: none;
    padding-left: 0;
    &.text-right {
      @media screen and (max-width: 991px) {
        text-align: left;
      }
    }
  }
  hr {
    border: none;
    border-bottom: 1px solid #777;
    margin-left: 45%;
    margin-right: 45%;
  }
}

.arrow-down-white {
  width: 0; 
  height: 0; 
  border-left: 40px solid transparent;
  border-right: 40px solid transparent;
  border-top: 40px solid #ffffff;
  margin: 0 calc(50% - 40px);
}

.bg-green {
  background: linear-gradient(#2EA155, #9DD0AA);
  color: #ffffff;
}

.cta .btn {
  font-size: 28px; 
  @media screen and (max-width: 767px) {
    font-size: 16px;
    white-space: normal;
  }
  border-radius: 1000px;
  padding: 10px 40px;
  margin-top: 10px;
  background-color: darken(#2EA155, 10%);
  color: #ffffff;
  transition: all 0.5s;
  &:hover {
    background-color: #ffffff;
    color: darken(#2EA155, 10%);
  }
  border: none;
}

.well {
  height: 150px;
  background: #ffffff !important;
  display: flex;
  justify-content: center;
  align-items: center;
  .logo-techlab {
    height: 100px;
  }
  p {
    margin: 0;
  }
  a {
    color: darken(#2EA155, 25%);
  }
}

.brought-to-you-by {
  a {
    color: #ffffff;
    transition: color 0.5s;
    &:hover {
      color: #2EA155;
      text-decoration: none;
    }
  }
}

@media screen and (max-width: 767px) {
  .embed {
    iframe {
      height: 200px;
    }
  }
}

